<template>
  <div class="section">
    <section class="container section-spacing">
      <div class="columns is-centered">
        <div class="column is-8">
          <h1 class="title is-1">{{ event.pageRSVPTitle }}</h1>
          <h2 class="subtitle is-4">
            {{ event.pageRSVPSubtitle }}
          </h2>
        </div>
      </div>
    </section>

    <section class="container section-spacing">
      <div class="columns is-centered content">
        <div class="column is-6">
          <Markdown :input="pageContent" />
        </div>
      </div>
    </section>

    <section class="container">
      <div class="columns is-centered">
        <div class="column is-6">
          <form @submit.prevent="rsvpForEvent">
            <h2 class="title has-text-poppins is-5 has-red-stripe">
              let's first confirm a few details about you
            </h2>

            <b-field
              label="First Name"
              :type="{ 'is-danger': $v.person.firstName.$error }"
              :message="
                $v.person.firstName.$error ? 'Please enter your first name' : ''
              "
            >
              <b-input
                v-model="person.firstName"
                placeholder="Leslie"
                @blur="$v.person.firstName.$touch()"
                type="text"
                icon="account"
              >
              </b-input>
            </b-field>

            <b-field
              label="Last Name"
              :type="{ 'is-danger': $v.person.lastName.$error }"
              :message="
                $v.person.lastName.$error ? 'Please enter your last name' : ''
              "
            >
              <b-input
                v-model="person.lastName"
                placeholder="Miles"
                @blur="$v.person.lastName.$touch()"
                type="text"
                icon="account"
              >
              </b-input>
            </b-field>

            <b-field
              label="Email"
              :type="{
                'is-danger': $v.person.email.$error || errors.email
              }"
              :message="errors.email ? errors.email : ''"
            >
              <b-input
                v-model="person.email"
                placeholder="francis@example.com"
                @blur="$v.person.email.$touch()"
                type="email"
                icon="email"
              >
              </b-input>
            </b-field>



     

            <fieldset>
              <legend class="label">
                Do you have any Dietary Requirements?
              </legend>
              <div class="checkbox-row">
                <b-checkbox
                  v-for="requirement in dietaryRequirements"
                  :key="requirement"
                  class="checkbox-column"
                  v-model="person.data.dietaryRequirements"
                  :native-value="requirement"
                >
                  {{ requirement }}
                </b-checkbox>
              </div>
            </fieldset>

            <b-field
              label="Will you be attending our Community Event?"
              :type="{
                'is-danger': $v.rsvp.attending.$error
              }"
              class="bottom-space"
              :message="
                $v.rsvp.attending.$error ? 'Please choose Yes or No' : ''
              "
            >
              <div class="rsvp-radios columns">
                <b-radio
                  v-model="rsvp.attending"
                  size="is-large"
                  :native-value="true"
                >
                  Yes
                </b-radio>
                <b-radio
                  v-model="rsvp.attending"
                  size="is-large"
                  :native-value="false"
                >
                  No
                </b-radio>
              </div>
            </b-field>

            <input
              class="button is-medium is-pulled-right is-primary"
              type="submit"
              :value="rsvpText"
            />
          </form>
        </div>
      </div>
    </section>

    <email-exists-modal
      @closeModal="emailExistsModalActive = false"
      :active="emailExistsModalActive"
      :email="person.email ? person.email : ''"
    >
      <p>
        You've already signed up with the email
        <strong>{{ person.email }}</strong
        >.
      </p>
      <p>
        We've sent an email to that address that will allow you to RSVP and
        update your details.
      </p>
    </email-exists-modal>
  </div>
</template>

<script>
import { required } from "vuelidate/lib/validators";
import Markdown from "@/components/Markdown";
import EmailExistsModal from "@/components/EmailExistsModal";
import { markdownSubstitutionsMixin } from "@/mixins/markdownSubstitutionsMixin";
import { mapGetters } from "vuex";

export default {
  name: "RSVPForEvent",
  components: {
    Markdown,
    EmailExistsModal
  },
  validations: {
    person: {
      firstName: { required },
      lastName: { required },
      email: { required },
    },
    rsvp: {
      attending: { required }
    }
  },
  mixins: [markdownSubstitutionsMixin],
  data() {
    return {
      emailExistsModalActive: false,
      errors: {}
    };
  },
  methods: {
    async rsvpForEvent() {
      this.$v.$touch();
      if (!this.$v.$invalid && !this.errors.email) {
        if (this.person.id) {
          await Promise.all([
            this.$store.dispatch("people/updatePerson", {
              person: {
                id: this.person.id,
                firstName: this.person.firstName,
                lastName: this.person.lastName,
                email: this.person.email
              },
              profile: this.person.data
            }),
            this.createOrUpdateRSVP(this.event.slug, this.person.id, this.rsvp)
          ]);

          this.$snackbar.open({
            message: this.snackbarMessage,
            position: "is-bottom-left"
          });
        } else {
          try {
            await this.$store.dispatch("people/createPerson", {
              person: {
                firstName: this.person.firstName,
                lastName: this.person.lastName,
                email: this.person.email,
                source: `${this.event.slug}-event-rsvp`
              },
              profile: this.person.data,
              page: {
                name: "event-rsvp",
                eventId: this.event.id
              }
            });

            const personId = this.$store.state.people.person.id;
            await this.$store.dispatch("rsvps/createRSVP", {
              eventSlug: this.event.slug,
              data: {
                personId: personId,
                attending: this.rsvp.attending
              }
            });

            this.$snackbar.open({
              message: this.snackbarMessage,
              position: "is-bottom-left"
            });
            this.$router.push({
              name: "rsvp",
              params: { personId: personId }
            });
          } catch (e) {
            this.errors = e.response.data.error;
            this.emailExistsModalActive = true;
          }
        }
      }
    },
    createOrUpdateRSVP(eventSlug, personId, rsvp) {
      if (rsvp.id) {
        return this.$store.dispatch("rsvps/updateRSVP", {
          eventSlug,
          personId,
          data: { attending: rsvp.attending }
        });
      } else {
        return this.$store.dispatch("rsvps/createRSVP", {
          eventSlug,
          data: {
            personId: personId,
            attending: rsvp.attending
          }
        });
      }
    }
  },
  computed: {
    alreadyRSVPd() {
      return !!this.rsvp.id;
    },
    rsvpText() {
      return this.alreadyRSVPd ? "Update RSVP" : "RSVP";
    },
    snackbarMessage() {
      return this.alreadyRSVPd
        ? "Thank you for updating your RSVP"
        : "Thank you for RSVPing";
    },
    ...mapGetters({
      event: "events/event",
      person: "people/person",
      rsvp: "rsvps/rsvp"
    }),
    dietaryRequirements() {
      return this.$store.state.dietaryRequirements;
    },
    previousEvents() {
      return this.$store.state.previousEvents;
    },
    birthYears() {
      return this.$store.state.birthYears;
    },
    genders() {
      return this.$store.state.genders;
    },
    pageContent() {
      return this.substituteContent(
        this.event.pageRSVPContentMarkdown,
        this.person,
        this.event
      );
    }
  }
};
</script>

<style scoped>
.rsvp-radios {
  margin: 1rem;
}
</style>
